.modalNeatherMap {
    position: absolute;
    left: 50px;
    top: 50px;
    background: white;
    border-radius: 5px;
    -webkit-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
    overflow: hidden;
    width: 200px;
    padding: 10px 15px;
    z-index: 10;
    .modalNeatherMap__title {
        margin-bottom: 10px;
    }
    .modalNeatherMap__description {
        margin-bottom: 10px;
    }
    .modalNeatherMap__cords {
        display: flex;
        justify-content: space-around;
        & span {
            font-size: 18px;
            font-family: 'Roboto-Bold';
        }
    }
}